
.NavBar {
  min-height: 10px;
  display: flex;
  flex-direction: row;
  padding: 10px;
  justify-content: space-evenly;
  z-index: 9999;
  color: rgb(230, 233, 245);
  width:100%; 
  text-transform: uppercase;
  font-weight: 300;
  letter-spacing: 1px;
  margin-bottom: 50px;
}

.App-header {
  width:100%;
}
.BodySplit{
  display: flex;
  flex-direction: row;
  justify-content: center; 
  min-height: 100vh;
}
.NavLink{
  padding:12px;
  color: white;
  font-size: large;
  text-decoration: none;
}
.NavLink:Hover{
  padding:12px;
  color: white;
  font-size: large;
  text-decoration: underline;
}
.Link{
  color: white;
  text-decoration: none;
}
.Link:Hover{
  color: white;
  text-decoration: underline;
}

.LogoArea {
  flex:1;
  display: flex;
  align-content: center;
  justify-content: center;
}
@media screen and (max-width: 800px) {
  .LogoArea{
    display: none;
  }
}

.ContentArea {
  padding-left:80px;
  padding-right:80px;
  background-color: rgb(70, 84, 163);
  flex:1;
}
.Selected{
  text-decoration: underline;
}

.Caption{
  display: flex;
  flex-direction: row;
  
 
}
.Caption>h1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.Joe{
  width:150px;
  margin-left: 50px;
  border-radius: 70px;
}
.Companies{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  
}

.Company {
  margin-bottom: 2px;
}
.bottom-container {
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: 48px;
}
.email{
  color:rgb(70, 84, 163);
  border: solid rgb(70, 84, 163) 2px;
  
  padding:12px;
  font-weight: 400;
  font-size: large;
  text-transform: uppercase;
  letter-spacing: 1px;
  text-decoration: none;

}
.email:hover{
  color:white;
  background-color: rgb(70, 84, 163);
  text-decoration: underline;
} 
.Logo-image{
  height: 80vh;
 
  flex-direction: column;
}